import React from 'react';
import ReactMarkdown from 'react-markdown';
import { Helmet } from 'react-helmet';
import gfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import { graphql } from 'gatsby';
import {
  FaEnvelope,
  FaFacebookF,
  FaLinkedinIn,
  FaQuoteLeft,
  FaTwitter,
  FaWhatsapp,
} from 'react-icons/fa';
import Layout from '../layouts';
import SingleFeatured from '../components/Banners/SingleFeatured';
import SEO from '../components/SearchEngine';
import * as styles from './singlefeatureprod.module.scss';

const checkWindow = typeof window !== 'undefined';

const SingleFeatureProd = ({ data }) => {
  const bannerData = {
    title: data.markdownRemark.frontmatter.title,
    author: data.markdownRemark.frontmatter.author,
    date: data.markdownRemark.frontmatter.date,
    excerpt: data.markdownRemark.frontmatter.excerpt,
  };

  const socials = [
    { path: '#', icon: <FaTwitter size={27} />, platform: 'Twitter' },
    { path: '#', icon: <FaFacebookF size={27} />, platform: 'FaceBook' },
    { path: '#', icon: <FaLinkedinIn size={27} />, platform: 'LinkedIn' },
    { path: '#', icon: <FaWhatsapp size={27} />, platform: 'Whatsapp' },
    { path: '#', icon: <FaEnvelope size={27} />, platform: 'Email' },
  ];

  const finalTitle = data.markdownRemark.frontmatter.title.toUpperCase();

  // HANDLE SHARE BUTTON CLICK

  function shareButtonClicked(e, platform) {
    if (platform !== 'Whatsapp' && platform !== 'Email') {
      if (checkWindow) {
        // eslint-disable-next-line no-undef
        window.open(
          e.target.href,
          '',
          'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600',
        );
        return false;
      }
    }

    if (platform === 'Whatsapp') {
      if (checkWindow) {
        // eslint-disable-next-line no-undef
        window.open(
          e.target.href,
          '_blank',
          'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600',
        );
        return false;
      }
    }

    if (platform === 'Email') {
      return false;
    }
    return false;
  }

  return (
    <Layout
      bannerHeight='700px'
      bannerStyle={{ backgroundImage: 'none' }}
      bannerChildren={<SingleFeatured featureData={bannerData} />}
      heroBackgroundColor='altlightest'
    >
      <SEO title={finalTitle} />
      <Helmet
        meta={[
          {
            name: `og:image`,
            content: data.markdownRemark.frontmatter.image_1.childImageSharp.fluid.src,
          },
        ]}
      />

      <section className='bg-altlightest w-100'>
        <div
          className={`row ${styles.featured_photo}`}
          style={{
            backgroundImage: `url(${data.markdownRemark.frontmatter.image_1.childImageSharp.fluid.src})`,
          }}
        />

        <div className={`${styles.article_container}`}>
          <div className={`row ${styles.article_text}`}>
            <ReactMarkdown remarkPlugins={[gfm]} rehypePlugins={[rehypeRaw]}>
              {data.markdownRemark.frontmatter.content}
            </ReactMarkdown>
          </div>
        </div>

        <div
          className={`row ${styles.featured_photo}`}
          style={{
            backgroundImage: `url(${data.markdownRemark.frontmatter.image_2.childImageSharp.fluid.src})`,
          }}
        />

        <div className={`${styles.article_container_large}`}>
          <p className={`${styles.quote}`}>
            <FaQuoteLeft className={`${styles.quote_icon}`} size={51} />
            {data.markdownRemark.frontmatter.quote_1}
          </p>

          <div
            className={`${styles.qna} px-3`}
            dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}
          />
        </div>

        <div className={`${styles.article_container}`}>
          {data.markdownRemark.frontmatter.about_author !== '' && (
            <div className={`${styles.about_author}`}>
              <h2>About the author</h2>

              <ReactMarkdown remarkPlugins={[gfm]} rehypePlugins={[rehypeRaw]}>
                {data.markdownRemark.frontmatter.about_author}
              </ReactMarkdown>
            </div>
          )}

          <p className={`${styles.contact_us}`}>
            Know someone we should feature? Email us at{' '}
            <a href='mailto:editors@theafricaiknow.org'>editors@theafricaiknow.org</a>
          </p>

          <div className={`${styles.share}`}>
            <h2>Share this article</h2>
            <div className='d-flex justify-content-center'>
              {socials.map(({ path, icon, platform }) => {
                if (platform === 'Whatsapp') {
                  return (
                    <a
                      className={`me-3 ${styles.social} ${styles.hideOnBigScreen}`}
                      href={path}
                      onClick={function (e, platform) {
                        shareButtonClicked(e, platform);
                      }}
                      style={{
                        '--social-button-color': `var(--color-white)`,
                        '--social-button-color-hover': `var(--color-dark-200)`,
                        '--social-button-size': `52px`,
                        '--social-button-margin': `18px`,
                      }}
                      rel='noreferrer'
                      title={`Share to ${platform}`}
                    >
                      {icon}
                    </a>
                  );
                }
                return (
                  <a
                    className={`me-3 ${styles.social}`}
                    href={path}
                    onClick={function (e, platform) {
                      shareButtonClicked(e, platform);
                    }}
                    style={{
                      '--social-button-color': `var(--color-white)`,
                      '--social-button-color-hover': `var(--color-dark-200)`,
                      '--social-button-size': `52px`,
                      '--social-button-margin': `18px`,
                    }}
                    rel='noreferrer'
                    title={`Share to ${platform}`}
                  >
                    {icon}
                  </a>
                );
              })}
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      frontmatter {
        id
        slug
        title
        excerpt
        author
        about_author
        category
        subject
        content
        quote_1
        quote_2
        image_1 {
          childImageSharp {
            fluid {
              src
            }
          }
        }
        image_2 {
          childImageSharp {
            fluid {
              src
            }
          }
        }
      }
    }
  }
`;

export default SingleFeatureProd;
